<script>
import axios from "axios";

export default {
  data() {
    // const item = {
    //   name: "busybox",
    //   type: "v2",
    //   status: "true",
    //   user: "",
    //   host: "192.168.109.2",
    //   port: 6999,
    // };
    return {
      loading: false,
      tableData: [],
    };
  },
  mounted() {
    this.loading=true
    let _this = this
    axios.get("/registry").then(function (response) {
      _this.loading=false
      let tableData = [];
      let registries = response.data.obj;
      console.log(response)
      registries.forEach((element) => {
        tableData.push({
          name: element.name,
          type: element.repositoryType,
          status: element.status,
          user: element.username,
          host: element.host,
          port:element.port,
        });
      });
      _this.tableData = tableData
    })
  },
  methods: {

  }
};
</script>


